
.coupon-get {
  color: #3C3E3A;
}
.coupon-get__input .weui-cells {
  margin-top: 0;
  border-radius: 5px;
}
.coupon-get__submit {
  margin-top: 15px;
}
.coupon-get__title {
  position: relative;
  height: 26px;
  width: 100%;
}
.coupon-get__title::before {
  position: absolute;
  background-color: #BDBCBA;
  width: 100%;
  left: 0;
  top: 12px;
  content: " ";
  height: 2px;
}
.coupon-get__title__text {
  position: absolute;
  width: 140px;
  left: 50%;
  margin-left: -70px;
  background-color: #D1E8A4;
  text-align: center;
  line-height: 26px;
  font-size: 16px;
}
.coupon-get__rules {
  margin: 15px 0;
  line-height: 1.75;
  list-style: none;
  font-size: 14px;
}



