
.person-index {
  position: relative;
  height: 100vh;
  background-image: url('../../assets/bg.png');
  background-size: 100% auto;
}
.person-index__btns {
  position: absolute;
  width: calc(100% - 30px);
  bottom: 14%;
  left: 15px;
}
.person-index__text {
  position: absolute;
  width: 100%;
  text-align: center;
  color: #fff;
  font-size: 15px;
  line-height: 1;
}
.person-index__text__name {
  top: 75px;
}
.person-index__text__price {
  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;
  top: 155px;
  font-size: 50px;
}
.person-index__text__tips {
  top: 220px;
}



