
.pay-history .vux-timeline {
  padding: 8px 0 0 15px;
}
.pay-history .vux-timeline-item-head,
.pay-history .vux-timeline-item-head-first,
.pay-history .vux-timeline-item-tail {
  top: 38px;
}
.pay-history .vux-timeline-item-content {
  padding: 0 0 8px 25px;
}
.pay-history__item {
  border: 1px solid #eaeaea;
  background-color: #fff;
  height: 75px;
  padding: 5px 12px;
}
.pay-history__item p {
  color: #8a8a8a;
  font-size: 13px;
  line-height: 2;
}
.pay-history__item__info {
  font-size: 12px;
  line-height: 2;
}



