
.person-login {
  padding: 0 25px;
}
.person-login__logo {
  text-align: center;
  padding-top: 37px;
  margin-bottom: 53px;
}
.person-login__logo img {
  width: 89px;
  height: 62px;
}
.person-login__inputs .weui-cells {
  margin-top: 0;
  background-color: #f4f5f9;
}
.person-login__inputs .weui-cells:before {
  content: none;
}
.person-login__inputs .weui-cell {
  padding: 15px 0 15px 15px;
  font-size: 14px;
}
.person-login__inputs .weui-cell::before {
  left: 0;
}
.person-login__inputs .weui-cell .weui-label {
  color: #5b5b5b;
}
.person-login__vcode {
  width: 100px !important;
  background-color: #feb92f;
}
.person-login__vcode:not(.weui-btn_disabled):active {
  background-color: #feb92f;
}
.person-login__vcode.weui-btn_disabled.weui-btn_default {
  color: rgba(0, 0, 0, 0.5);
  background-color: rgba(254, 198, 47, 0.8);
}
.person-login__agree {
  margin-top: 8px;
  padding-left: 8px;
}
.person-login__agree .weui-icon-success:before {
  color: #feb92f !important;
}
.person-login__agree__text {
  font-size: 12px;
  color: #a9a9a9;
}
.person-login__agree__text a {
  color: #8a8a8a;
}
.person-login__agree__text a:active,
.person-login__agree__text a:hover {
  color: #feb92f;
}
.person-login__submit {
  margin-top: 40px;
  background-color: #feb92f;
  font-size: 14px;
  line-height: 40px;
}
.person-login__submit:not(.weui-btn_disabled):active {
  background-color: #feb92f !important;
}
.person-login__submit.weui-btn_disabled.weui-btn_default {
  color: rgba(0, 0, 0, 0.5);
  background-color: rgba(254, 198, 47, 0.8);
}



