
.person-list__search {
  position: fixed;
  width: 100%;
  z-index: 5;
  top: 0;
  left: 0;
  right: 0;
}
.person-list__search__icon {
  padding-right: 8px;
}
.person-list__search .weui-search-bar__box .weui-search-bar__input {
  line-height: 26px;
  height: 26px;
}
.person-list__search .weui-search-bar__box .weui-icon-search {
  line-height: 34px;
}
.person-list__search .weui-search-bar__cancel-btn {
  line-height: 34px;
}
.person-list__search .weui-search-bar {
  padding: 8px 10px;
}
.person-list__data-list {
  margin-top: 50px;
}
.person-list__no-data.weui-loadmore_line {
  margin-top: 100px;
}
.person-list__item {
  padding: 10px;
  position: relative;
  border-top: 1px solid #eaeaea;
  border-bottom: 1px solid #eaeaea;
  background-color: #fff;
  margin-bottom: 10px;
}
.person-list__item:first-child {
  border-top: 0;
}
.person-list__item:last-child {
  margin-bottom: 0;
}
.person-list__item__icon {
  text-align: center;
  position: absolute;
  right: 15px;
  top: 10px;
  line-height: 1;
  text-align: right;
}
.person-list__item__icon p {
  margin-top: 4px;
  font-size: 15px;
  color: #feb92f;
}
.person-list__item__img {
  width: 80px;
  height: 80px;
  margin-right: 10px;
}
.person-list__item__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-color: #f4f5f9;
}
.person-list__item__info h4 {
  font-size: 15px;
  font-weight: normal;
  padding-right: 60px;
  padding-left: 3px;
}
.person-list__item__info p {
  font-size: 12px;
  margin-top: 6px;
  color: #8a8a8a;
}
.person-list__scan {
  position: fixed;
  bottom: 60px;
  width: 160px;
  left: 50%;
  margin-left: -80px;
}
.person-list__scan .car-btn {
  box-shadow: 2px 3px 4px rgba(184, 184, 184, 0.75);
}



