
.coupon {
  position: relative;
  min-height: 100vh;
  background-color: #8EC31D;
}
.coupon::after {
  display: block;
  content: " ";
  clear: both;
  height: 1px;
}
.coupon__img {
  width: 100%;
}
.coupon__main {
  margin: 0 10px 10px;
}
.coupon__footer {
  height: 1px;
  width: 100%;
}
.coupon__item {
  padding: 17px 20px;
  border-radius: 8px;
  background-color: #D1E8A4;
}
.coupon__item:first-child {
  margin-bottom: 10px;
}
.coupon__item:last-child {
  margin-bottom: 0;
}
.coupon__item__button {
  margin-top: 15px;
}



