
.coupon-detail__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.coupon-detail__list > li {
  background-color: #fff;
  margin-bottom: 5px;
  padding: 0 25px;
  position: relative;
}
.coupon-detail__list > li:last-child {
  margin-bottom: 0;
}
.coupon-detail__list > li::before,
.coupon-detail__list > li::after {
  position: absolute;
  top: 32px;
  background-color: #D1E8A4;
  content: " ";
  clear: both;
  height: 14px;
  width: 14px;
  border-radius: 50%;
}
.coupon-detail__list > li::before {
  left: -7px;
}
.coupon-detail__list > li::after {
  right: -7px;
}
.coupon-detail__item {
  line-height: 38px;
  font-size: 12px;
  color: #747474;
}
.coupon-detail__item:first-child {
  font-size: 15px;
  color: #CA3C13;
  border-bottom: 1px solid #BFBFBF;
  position: relative;
}
.coupon-detail__item__price {
  color: #F83018;
  font-size: 30px;
  position: absolute;
  line-height: 1;
  right: 2px;
  bottom: 3px;
}
.coupon-detail__item__price > small {
  font-size: 18px;
}
.coupon-detail__account {
  margin-top: 15px;
  text-align: center;
  font-size: 12px;
  color: #5D5A5A;
}



