html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
.color-yellow {
  color: #feb92f !important;
}
.color-blue {
  color: #5cbac6 !important;
}
.bg-yellow {
  background-color: #feb92f !important;
}
.bg-blue {
  background-color: #5cbac6 !important;
}
.ellipsis {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
/*浮动*/
.left {
  float: left !important;
}
.right {
  float: right !important;
}
.svg-icon {
  vertical-align: middle;
  transform: translateY(-1px);
}
.car-btn {
  border-radius: 22px !important;
  color: #fff !important;
  font-size: 15px !important;
  line-height: 45px !important;
}
.car-btn.weui-btn:after {
  border: 0 !important;
}
.car-btn svg {
  margin-right: 10px;
}
.car-btn__blue {
  background-color: #5cbac6 !important;
}
.car-btn__blue:not(.weui-btn_disabled):active {
  background-color: #5cbac6 !important;
  color: #fff !important;
}
.car-btn__yellow {
  background-color: #feb92f !important;
}
.car-btn__yellow:not(.weui-btn_disabled):active {
  background-color: #feb92f !important;
  color: #fff !important;
}
.weui-toast__content {
  font-size: 13px !important;
}



