
.pay-index {
  padding: 25px;
}
.pay-index__btn {
  height: 65px;
  text-align: center;
  margin-right: 16px;
  margin-bottom: 16px;
  color: #feb92f;
  background-color: #fff;
  border-radius: 5px;
  border: 1px solid #eaeaea;
}
.pay-index__btn--active {
  color: #fff;
  background-color: #feb92f;
  border: 1px solid #feb92f;
}
.pay-index__btn h3 {
  line-height: 1;
  padding-top: 12px;
  font-weight: normal;
}
.pay-index__btn h5 {
  line-height: 1;
  padding-top: 10px;
  font-weight: normal;
}
.pay-index__btn:nth-child(n+3) {
  margin-right: 0;
}
.pay-index__btn__pay {
  margin-top: 35px;
  background-color: #feb92f;
  color: #fff;
  border-radius: 20px;
  line-height: 42px;
  font-size: 16px;
}
.pay-index__btn__pay.weui-btn:after {
  border: 0;
}
.pay-index__btn__pay:not(.weui-btn_disabled):active {
  background-color: #feb92f;
  color: #fff;
}
.pay-index__btn__pay.weui-btn_disabled.weui-btn_default {
  color: #fff;
  background-color: rgba(254, 198, 47, 0.8);
}



